#sidebarDrawer {
	background: var(--body-bg-color);
	@include fixed(0,-100%,n,n);
	width: 100%;
	height: 100vh;
	z-index: layer(sidebarDrawer);
	@extend %transition;
	&.is-open {
		@include left(0);
	}
	.sidebarDrawer-close {
		@include ab(20px,n,n,15px);
		width: 40px;
		height: 40px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 5px;
		background: var(--main-color);
		z-index: layer(sidebarDrawer-close);
		svg {
			width: 16px;
			height: 16px;
			path {
				fill: var(--text-miscellaneous);
			}
		}
		&:hover {
			@media (hover:hover) {
				opacity: 0.8;
			}
		}
	}
	.section_content-overlay {
		@extend %transition;
	}
	&.is-open {
		.section_content-overlay {
			width: 100vw;
			height: 100vh;
			background: rgba(0,0,0,.6);
			@include fixed(0,0,n,n);
			@media (min-width: 992px) {
				display: none;
			}
		}
	}
	.section_content-wrapper {
		scrollbar-color: var(--main-color) var(--body-border-color);
		scrollbar-width: thin;
		padding-bottom: 25px;
		position: relative;
		background: var(--miscellaneous-color);
		@media (min-width: 992px) {
			background: none;
		}
	}
	.section_content-body {
		overflow: hidden;
		overflow-y: auto;
		max-height: 100vh;
		padding: 0 20px 25px;
	}
	.section_content-body::-webkit-scrollbar-track {
		@extend %webkit-scrollbar-track;
	}
	.section_content-body::-webkit-scrollbar {
		@extend %webkit-scrollbar;
	}
	.section_content-body::-webkit-scrollbar-thumb {
		@extend %webkit-scrollbar-thumb;
	}
	@media (min-width: 375px) {
		width: 360px;
	}
}
.featured_block .sb-title{
	span {
		position: relative;
		&:before {
			content: "";
			@include ab(n,0,-16px,0);
			max-width: 80%;
			min-width: 40px;
			height: 2px;
			background: var(--main-color);
		}
	}
}
.featured_block-search {
	.input-group {
		background: var(--miscellaneous-color);
		position: relative;
		input {
			@include padding-end(40px);
		}
		button {
			@include ab(0,n,n,0);
			width: 40px;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			svg {
				width: 18px;
				height: 18px;
				color: var(--body-second-color);
			}
		}
	}
}
.featured_block-blog-list {
	article {
		display: flex;
		margin-bottom: 24px;
		.image {
			max-width: 80px;
			width: 100%;
			height: 80px;
			overflow: hidden;
			@include margin-end(15px);
			span {
				height: 100%;
				img {
					object-fit: cover;
				}
			}
		}
		.title {
			font-size: 15px;
			line-height: 1.618;
		}
		.published-at {
			font-size: 12px;
			margin-top: 10px;
			color: var(--main-color);
		}
	}
}
.featured_block-tags {
	.tag-list {
		display: flex;
		flex-wrap: wrap;
		li {
			a {
				padding: 9px 18px;
				background: var(--body-link);
				display: block;
				margin-bottom: 10px;
				font-size: var(--body-14);
				@include margin-end(10px);
				@extend %radius-4px;
				@extend %transition;
				color: var(--text-miscellaneous);
				&:hover {
					@media (hover:hover) {
						background: var(--main-color);
					}
				}
			}
		}
	}
}
.page-blog {
	.blog-title {
		font-weight: 500;
		margin-bottom: 30px;
	}
	.featured_blog__item {
		background: var(--miscellaneous-color);
		margin-bottom: 25px;
		footer {
			padding: 20px;
		}
		.featured_blog__item-title {
			font-size: 20px;
			margin-bottom: 15px;
			a {
				color: #333e48;
				&:hover {
					@media (hover:hover) {
						color: var(--main-color);
					}
				}
			}
		}
		.featured_blog__item-author {
			text-transform: uppercase;
			margin-bottom: 20px;
			letter-spacing: 0.4px;
			font-size: 12px;
			.featured_blog__item-publish {
				color: var(--main-color);
			}
		}
		.featured_blog__item-excerpt {
			margin-bottom: 35px;
			line-height: 22px;
			@include clamp(inherit);
		}
	}
	.page_wrapper {
		.btn-more {
			display: flex;
			justify-content: center;
			margin: 0 -15px;
			margin-top: 40px;
			@extend %radius-reset;
		}
	}
	.col-4 {
		.featured_blog__item {
			margin-bottom: 30px;
			footer {
				padding: 30px 15px;
			}
		}
	}
	@media (min-width: 768px) {
		.featured_blog__item {
			footer {
				padding: 35px;
			}
			.featured_blog__item-title {
				font-size: var(--heading-size-h3);
			}
		}
	}
	@media (min-width: 992px) {
		margin-bottom: 40px;
		}
	}
	.page-blog, .page-article {
		@media (min-width: 992px) {
			#sidebarDrawer {
				position: unset;
				height: 100%;
				transition: none;
				width: 100%;
				.section_content-wrapper {
					padding-left: 0;
					padding-right: 0;
				}
				.section_content-body {
					overflow: hidden;
					max-height: 100%;
					padding: 0;
				}
			}
		}
		@media (min-width: 1200px) {
			#sidebarDrawer { 
				.featured_block-category {
					border: 2px solid var(--main-color);
					background: var(--miscellaneous-color);
					.sb-title {
						border-bottom: 1px solid var(--main-color);
						background: var(--main-color);
						padding: 15px 25px;
					}
					.menu_list-wrapper {
						padding: 0 25px 15px;
						>li:not(:last-child) {
							border-bottom: 1px solid var(--body-border-color);
						}
						>li {
							padding: 10px 0;
						}
					}
				}
			}
		}
	}
/*page article*/
.page-article {
	.article-content {
		background: var(--miscellaneous-color);
		margin-bottom: 60px;
		.title {
			font-size: var(--heading-size-h3);
			font-weight: 500;
			line-height: 1.2;
			margin-bottom: 10px;
		}
		.author {
			text-transform: uppercase;
			font-size: 12px;
			letter-spacing: 0.2px;
			line-height: 25px;
			margin-bottom: 15px;
		}
		.published_at {
			margin: 0 8px;
			color: var(--main-color);
		}
		.content {
			font-size: var(--body-14);
			color: var(--color-second);
			p {
				margin: 0 0 25px;
				line-height: 1.618;
			}
			ul {
				margin-bottom: 30px;
				@include margin-start(20px);
				li {
					list-style: inherit;
					font-weight: 300;
					margin: 5px 0;
				}
			}
			blockquote {
				font-size: 15px;
				padding: 40px 10% 20px;
				position: relative;
				&:before {
					content: "";
					background-image: url("data:image/svg+xml,%3Csvg aria-hidden='true' focusable='false' data-prefix='fas' data-icon='quote-left' class='svg-inline--fa fa-quote-left fa-w-16' role='img' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'%3E%3Cpath fill='%23fdb819' d='M464 256h-80v-64c0-35.3 28.7-64 64-64h8c13.3 0 24-10.7 24-24V56c0-13.3-10.7-24-24-24h-8c-88.4 0-160 71.6-160 160v240c0 26.5 21.5 48 48 48h128c26.5 0 48-21.5 48-48V304c0-26.5-21.5-48-48-48zm-288 0H96v-64c0-35.3 28.7-64 64-64h8c13.3 0 24-10.7 24-24V56c0-13.3-10.7-24-24-24h-8C71.6 32 0 103.6 0 192v240c0 26.5 21.5 48 48 48h128c26.5 0 48-21.5 48-48V304c0-26.5-21.5-48-48-48z'%3E%3C/path%3E%3C/svg%3E");
					@include ab(0,3%,n,n);
					width: 40px;
					height: 40px;
				}
			}
			.hover {
				text-align: center;
				position: relative;
				overflow: hidden;
				img {
					transform: translate3d(0,0,0);
					@extend %transition;
				}
				span {
					@include ab(n,0,0,0);
					transform: translate3d(0,40px,0);
					opacity: 0;
					@extend %transition;
					font-size: var(--body-14);
				}
				&:hover {
					@media (hover:hover) {						
						span {
							opacity: 1;
							transform: translate3d(0,0,0);
						}
						img {
							transform: translate3d(0,-40px,0);
						}
					}
				}
			}
		}
		.share-links {
			margin-bottom: 20px;
			margin-top: 35px;
			.list-inline {
				@include margin-start(10px);
			}
			.social-item {
				margin: 0 2px;
				svg {
					width: 30px;
					height: 30px;
				}
			}
		}
		.pagination {
			padding-top: 30px;
			border-top: 1px solid var(--body-border-color);
			font-size: var(--body-14);
			line-height: 24px;
			svg {
				width: 12px;
				height: 12px;
			}
			.next svg {
				@include margin-start(5px);
			}
			.prev svg {
				@include margin-end(5px);
			}
		}
	}
	.article-content_body {
		padding: 20px 15px;
	}
	.related-posts {
		margin-bottom: 60px; 
		.article-item__wrapper {
			display: flex;
			background: var(--miscellaneous-color);
			.image {
				width: 40%;
				height: 180px;
				span {
					height: 100%;
				}
			} 
			.info {
				width: 60%;
				padding: 20px 15px;
			}
			.info_header {
				text-transform: uppercase;
				font-size: 13px;
				margin-bottom: 15px;
			}
			.published_at {
				@include margin-end(8px);
				color: var(--main-color);
			}
			.info_name {
				font-size: 16px;
				font-weight: 500;
				margin-bottom: 15px;
				@include clamp(2);
			}
			.category {
				color: var(--color-second);
			}
			.info_content {
				@include clamp(2);
				font-size: var(--body-14);
			}
		}
	}
	.comment-content {
		margin: 60px 0;
		.comment-content_title {
			font-size: var(--heading-size-h3);
			margin-bottom: 35px;
		}
		.comment-item {
			background: var(--miscellaneous-color);
			padding: 20px;
			margin-bottom: 15px;
			.avatar {
				width: 60px;
				@include margin-end(30px);
				margin-bottom: 30px;
				img {
					@extend %radius-circle;
				}
			}
			.text {
				flex: 1;
				.info {
					margin-bottom: 20px;
					display: flex;
					align-items: center;
					.name {
						font-size: 16px;
						font-weight: 500;
						@include margin-end(3px);
					}
					.published-at {
						color: var(--body-second-color);
						font-size: 12px;
						@include margin-start(3px);
					}
				}
				p {
					font-size: 15px;
					margin: 0;
					line-height: 1.618;
				}
			}
		}
	}
	.comment-form {
		margin: 60px 0;
		font-size: 15px;
		.form-headline {
			margin-bottom: 30px;
			.title {
				margin-bottom: 10px;
			}
		}
		input {
			background: var(--miscellaneous-color);
		}
		textarea {
			background: var(--miscellaneous-color);
			height: 160px;
		}
		.btn {
			margin-top: 20px;
		}
	}
	@media (min-width: 768px) {
		.article-content {
			.content {
				font-size: 16px;
				.blockquote {
					font-size: 16px;
				}
			}
			.pangination {
				font-size: 16px;
			}
		}
		.article-content_body {
			padding: 30px;
		}
		.article-content .content {
			blockquote {
				font-size: 20px;
			}
		}
		.comment-form {
			.input-group {
				margin-bottom: 20px;
			}
		}
		.comment-content {
			.comment-item {
				padding: 30px;
				display: flex;
			}
		}
		.related-posts .article-item__wrapper {
			.info_content {
				font-size: 15px;
			}
		}
	}
	@media (min-width: 992px) {
		.related-posts .article-item__wrapper .info {
			padding-left: 25px;
			padding-right: 25px;
		}
	}
}
/*end page article*/